import * as React from "react"
import { useEffect, useState } from "react"
import { cx } from "@emotion/css"
import * as Color from "color"
import axios from "axios"

import { IFormField } from "../../data/shared"

import theme from "../../constants/theme"
import { InputGroup, TextAreaGroup, Button } from "../"

/**
 * ContactFormSection
 */
type Props = {
  getFormEndPoint: string
  formFields?: IFormField[]
}

export default function ContactForm({
  getFormEndPoint,
  formFields = [
    { element: "input", name: "name", label: "Name", placeholder: "Your name" },
    { element: "input", name: "email", label: "Email", placeholder: "Your email" },
    {
      element: "textarea",
      name: "message",
      label: "Message",
      placeholder: "Please let us know how we can help you.",
    },
  ],
}: Props) {
  const [isFormValid, setFormValid] = useState(false)
  // TODO: Make this dynamic (based on formFields[]).
  const [formData, setFormData] = useState({
    name: "",
    email: "",
    message: "",
  })
  const [serverStatus, setServerStatus] = useState({
    isSubmitting: false,
    success: null,
  })

  useEffect(() => {
    checkFormValidation()
  }, [formData])

  function resetForm() {
    setFormValid(false)
    // TODO: Make this dynamic (based on formFields[]).
    setFormData({
      name: "",
      email: "",
      message: "",
    })
    setServerStatus({
      isSubmitting: false,
      success: null,
    })
  }

  function checkFormValidation() {
    const emailRegex = /^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,}$/i
    emailRegex.test(formData.email) &&
    formData.name !== "" &&
    formData.email !== "" &&
    formData.message !== ""
      ? setFormValid(true)
      : setFormValid(false)
  }

  function handleServerResponse(success: any, form: any) {
    setServerStatus({
      isSubmitting: false,
      success: null,
    })
    if (success) {
      form.reset()
    }
  }

  function updateForm(e: any) {
    const { name, value } = e.target
    setFormData({ ...formData, [name]: value })
  }

  function submitForm(e: any) {
    e.preventDefault()

    let form = e.target
    setServerStatus({ isSubmitting: true, success: null })

    axios({
      method: "post",
      url: getFormEndPoint,
      data: new FormData(form),
    })
      .then(_ => {
        handleServerResponse(true, form)
      })
      .catch(_ => {
        handleServerResponse(false, form)
      })
  }

  return (
    <div className="w-full p-30 md:p-40 lg:p-60 bg-bg3 rounded-md">
      {serverStatus.success ? confirmation() : form()}
    </div>
  )

  function form() {
    if (!formFields || formFields.length === 0) {
      return null
    }

    return (
      <form onSubmit={submitForm}>
        <div className="grid grid-cols-1 gap-20 md:gap-30 lg:gap-40">
          {formFields.map(field => {
            return field.element === "input" ? (
              <InputGroup
                onChange={updateForm}
                value={formData[field.name]}
                key={field.name}
                type="text"
                name={field.name}
                label={field.label}
                placeholder={field.placeholder}
              />
            ) : field.element === "textarea" ? (
              <TextAreaGroup
                onChange={updateForm}
                value={formData[field.name]}
                key={field.name}
                name={field.name}
                label={field.label}
                placeholder={field.placeholder}
              />
            ) : null
          })}
          <button
            type="submit"
            disabled={!isFormValid || serverStatus.isSubmitting}
            css={{
              backgroundColor: theme.colors.accent2,
              "&:hover": {
                backgroundColor:
                  !isFormValid || serverStatus.isSubmitting
                    ? theme.colors.accent2
                    : theme.colors.accent2_hover,
              },
            }}
            className={cx(
              !isFormValid ? "opacity-50" : "opacity-100",
              "inline-block outline-none w-full h-60 md:h-80 rounded-md"
            )}>
            <span className="text-sm3 md:text-md text-altText1">
              {serverStatus.isSubmitting ? `One moment please` : `Send`}
            </span>
          </button>
        </div>
      </form>
    )
  }

  function confirmation() {
    return (
      <div className="flex flex-col py-40 md:py-80">
        <img
          className="w-full h-auto max-h-240 object-contain object-center"
          src={"/static/images/graphics/illustrations/illustration-home-header.svg"}
          alt={`props.confirmation.title`}
        />
        <h3 className="text-md2 text-text1 leading-none text-center w-auto md:w-8/12 lg:w-6/12 m-auto">
          {`props.confirmation.title`}
        </h3>
        <div className="h-20 bg-transparent" />
        <p className="text-md3 text-text3 text-center leading-relaxed w-auto md:w-8/12 lg:w-6/12 m-auto">
          {`props.confirmation.description`}
        </p>
        <div className="h-40 bg-transparent" />
        <div className="m-auto">
          {/* <Button
            onClick={resetForm}
            mode="button"
            label={`props.confirmation.buttonTitle`}
            frameColor={theme.colors.accent1}
            frameColorHover={theme.colors.accent1_hover}
            size={`lg`}
            isStretch={false}
          /> */}
        </div>
      </div>
    )
  }
}
